import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, onlyAlphaNum, WindowComponent } from 'summer';
import { CLASS_NAME, TipoCredito } from '../../../../models/dtos/ger/tipoCredito';

interface TipoCreditoEditViewProps extends EditViewProps<TipoCredito> {}

const TipoCreditoEditView: FC<TipoCreditoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<TipoCredito>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Manutenção de Tipo de Crédito">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, ...onlyAlphaNum(0, 3) } }}
              id="codigo"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 200 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default TipoCreditoEditView;
